 !important;\n                    }\n                    &:hover {\n                        i {\n                            color: #ffffff;\n                        }\n                    }\n                    &.active {\n                        border-left: solid 2px lighten($primary, 60%);\n                        i {\n                            color: #ffffff;\n                            font-weight: 500;\n                        }\n                    }\n                }\n                .lambda-nav-more{\n                    overflow: initial!important;\n                    ul{\n                        display: none;\n                        position: absolute!important;\n                        left: 79px!important;\n                        bottom: 0!important;\n                        z-index: 9999999!important;\n                    }\n                }\n                .lambda-nav-more:hover{\n                    ul{\n                        display: block;\n                    }\n                }\n            }\n        }\n    }\n\n    .sidebar-tools {\n        border-top: solid 1px lighten($primary, 10%);\n        margin-top: 20px;\n    }\n\n    .aside-bottom {\n        display: block;\n        margin-top: auto;\n        ul {\n            display: block;\n            width: 100%;\n            li {\n                display: block;\n                a {\n                    display: block;\n                    text-align: center;\n                    padding: 16px 0;\n                    border-top: solid 1px darken($primary, 3%);\n                    cursor: pointer;\n                    &.active,\n                    &:hover {\n                        i {\n                            color: #ffffff;\n                        }\n                    }\n                    i {\n                        color: $text-color;\n                        font-size: 20px;\n                    }\n                }\n            }\n        }\n    }\n    &.with-text {\n        width: 90px;\n        nav {\n            ul {\n                li {\n                    padding: 0 0 1px 0;\n                    a {\n                        position: relative;\n                        text-align: center;\n                        overflow-y: hidden;\n                        display: flex;\n                        flex-direction: column;\n                        justify-content: center;\n                        padding: 14px 0;\n                        color: $primary;\n                        transition: all .3s;\n                        i {\n                            color: $text-color;\n                            display: block;\n                            font-size: 22px;\n                            margin-bottom: 6px;\n                            transition: all .3s;\n                        }\n                        span:not(.ivu-badge) {\n                            color: $text-color;\n                            font-size: 11px;\n                            font-weight: 500;\n                            line-height: 12px;\n                            padding-left: 5px;\n                            padding-right: 5px;\n                            display: block;\n                            transition: all .3s;\n                        }\n\n                        .ivu-badge {\n                            position: absolute;\n                            top: 3px;\n                            right: 10px;\n                        }\n\n                        &:hover {\n                            background: lighten($primary, 5%);\n                            i {\n                                color: #ffffff;\n                            }\n                        }\n                        &.active {\n                            background: lighten($primary, 5%);\n                            span, i {\n                                color: #ffffff;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n    &.wide {\n        width: 200px;\n        nav {\n            ul {\n                li {\n                    padding: 0 0 1px 0;\n                    &.divider {\n                        height: 1px;\n                        background: lighten($primary, 5%);\n                        margin-top: 10px;\n                    }\n                    &.sub-title {\n                        font-size: 10px;\n                        color: lighten($primary, 40%);\n                        margin-top: 10px;\n                        span {\n                            padding-left: 10px;\n                        }\n                    }\n                    a {\n                        position: relative;\n                        overflow-y: hidden;\n                        display: flex;\n                        padding: 8px 15px;\n                        color: $primary;\n                        transition: all .3s;\n                        align-items: center;\n                        border-left: 0;\n                        border-top-right-radius: 15px;\n                        border-bottom-right-radius: 15px;\n                        width: 190px;\n                        margin: 5px 10px 0 0;\n                        i {\n                            color: $text-color;\n                            display: block;\n                            font-size: 22px;\n                            transition: all .3s;\n                            margin-right: 6px;\n                        }\n                        span:not(.ivu-badge) {\n                            color: $text-color;\n                            font-size: 11px;\n                            //font-weight: 500;\n                            line-height: 12px;\n                            padding-left: 5px;\n                            padding-right: 5px;\n                            display: block;\n                            transition: all .3s;\n                        }\n\n                        .ivu-badge {\n                            position: absolute;\n                            top: 3px;\n                            right: 10px;\n                        }\n\n                        &:hover {\n                            background: lighten($primary, 20%);\n\n                            i {\n                                color: #ffffff;\n                            }\n                        }\n                        &.active {\n                            background: lighten($primary, 5%);\n                            span, i {\n                                color: #ffffff;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n    &.hover {\n        width: 70px !important;\n        nav {\n            ul {\n                li {\n                    padding: 0 0 1px 0;\n                    a {\n                        position: relative;\n                        display: block;\n                        text-align: center;\n                        height: $anchor-height;\n                        overflow-y: hidden;\n                        padding: 0;\n                        // border-left: 0;\n                        i {\n                            color: $text-color;\n                            display: inline-block;\n                            line-height: $anchor-height;\n                            height: $anchor-height;\n                            width: 100%;\n                            font-size: 22px;\n                            -webkit-transition: margin-top 0.2s;\n                            transition: margin-top 0.2s;\n                            margin: 0;\n                        }\n                        .ivu-badge {\n                            position: absolute;\n                            top: 3px;\n                            right: 10px;\n                        }\n                    